import { Component, OnInit ,Output,Input,ChangeDetectorRef,OnChanges,EventEmitter} from '@angular/core';  

@Component({
  selector: 'app-page-hero-area',
  templateUrl: './page-hero-area.component.html',
  styleUrls: ['./page-hero-area.component.scss']
})
export class PageHeroAreaComponent implements OnChanges {

  @Input() headerData:object;
  @Input() fixed:boolean;
  @Output() onIconClick_e: EventEmitter<any> = new EventEmitter();

  private info : Array<string>;
  private icon:string;
  private showDemoBig:boolean;
  private showDemoSmall:boolean;
  private showIconBig:boolean;
  private showIconSmall:boolean;
  private demoLinkText:string;
  private demoLink:string;
  private isFixed:boolean;
  private isIcon:boolean;  
  private showYt:boolean;
  private videoLink:string;
 

  constructor(private _cd:ChangeDetectorRef) { }

  ngOnChanges() {
    this.info=this.headerData['info']||[];
    this.icon=this.headerData['icon']||'';
    this.videoLink =this.headerData['videoLink']||'';
    this.showDemoBig=this.headerData['showDemoBig']||false;
    this.getSwitch(this.headerData['showDemoSmall'] )
    this.demoLink=this.headerData['demoLink']||'#';
    this.demoLinkText=this.headerData['demoLinkText']||'';
    this.isIcon=(this.headerData && this.headerData['icon'])?true:false; 
    this.isFixed = (this.fixed)?this.fixed:false;
    this.showIconBig=this.headerData['showIconBig']||false;
    this.showIconSmall=this.headerData['showIconSmall']||false;
    
  }
  getSwitch(val){
    this.showDemoSmall=false;
    setTimeout(()=>{
      this.showDemoSmall=val;
    },0)
     
  }
  onCloseYt(event){
    this.showYt = false;
  }
  onOpenYt(){
    this.showYt = true;
  }
  onSmallIconClick(){
    this.onIconClick_e.emit(true)
  }
}
