import { Component, OnInit, Input,Output,EventEmitter } from '@angular/core';


@Component({
  selector: 'app-you-tube',
  templateUrl: './you-tube.component.html',
  styleUrls: ['./you-tube.component.scss']
})
export class YouTubeComponent implements OnInit {

  @Input() videoLink:string;
  @Input() size:Array<any>;
  @Output() ytClose_e: EventEmitter<any> = new EventEmitter();

    height:any;
    width:any;

  constructor() { }

  ngOnInit() {

  this.height=this.size[0];
  this.width=this.size[1];

  this.videoLink="https://www.youtube.com/embed/"+this.videoLink+"?rel=0";

  }

  onYtClose(){
    this.ytClose_e.emit(true)
  }



}
