import { Component, OnInit , Input,DoCheck} from '@angular/core';
import { tick } from '@angular/core/testing';

@Component({
  selector: 'app-code-display',
  templateUrl: './code-display.component.html',
  styleUrls: ['./code-display.component.scss']
})
export class CodeDisplayComponent implements OnInit ,DoCheck{

  @Input() display:any 
  private preData:any;
  private showCopied:boolean;
  constructor() { }
   
  ngOnInit() {
    this.showCopied =false;
  }
  ngDoCheck(){

    this.preData =  this.display.toString().replace(/  /g,'') ;
  }


  onSelect(elem){
    //let temp = document.querySelector('#outPut') as HTMLPreElement;
    let temp = elem;
    var range = document.createRange();
    range.selectNodeContents(temp);
    var sel = window.getSelection();
    sel.removeAllRanges();
    sel.addRange(range);
  }
  onCopy(elem){
    this.onSelect(elem);
    document.execCommand('copy');

    this.showCopied =true;
    setTimeout(()=>{
      this.showCopied = false;
    },2000)
   // <HTMLInputElement>this.outPut.nativeElement.select();
  }

  onDownload(elem){

    this.onSelect(elem);
    //let temp = document.querySelector('#outPut') as HTMLPreElement;
    let temp = elem
    var textToWrite = temp.innerText;
    var textFileAsBlob = new Blob([textToWrite], { type: 'text/plain' });
    //let date = this._datePipe.transform(new Date()).replace(/' '/g,"_");
    let date = new Date();
    var fileNameToSaveAs = "ServerDummies_" + date.getMonth() + '_' + date.getDate() + '_'+ date.getFullYear() + '_'+ date.getTime() + ".txt";
    var downloadLink = document.createElement("a");
    downloadLink.download = fileNameToSaveAs;
    downloadLink.innerHTML = "My Hidden Link";
    window.URL = window.URL;
    downloadLink.href = window.URL.createObjectURL(textFileAsBlob);
    downloadLink.onclick = this.destroyClickedElement;
    downloadLink.style.display = "none";
    document.body.appendChild(downloadLink);
    downloadLink.click();

  }

 destroyClickedElement(event) {
    document.body.removeChild(event.target);
}
  

}
